// Gatsby supports TypeScript natively!
import React from "react"
import { PageProps, Link } from "gatsby"

import Layout from "../../components/layout"
import SEO from "../../components/seo"
import { Container, Row, Col } from "react-bootstrap"

const OurLocation = () => (
    <Layout>
        <SEO title="Our Location" />
        <div className="jumbotron">
 
            <Container>
                <Row>
                    <Col><h1>Our Location</h1></Col>
                </Row>
                <Row>
                    <Col>
                        <h2>History</h2>
                    </Col>
                </Row>
                <Row>
                    <Col>
                        <p>
                        Originally a copper miner’s cottage, parts of which date back to the early 1800’s, Rhylla Cottage stands high above the Tamar Valley with its outstanding beautiful views, in an area of outstanding natural beauty (AONB).
                        It is situated in the small, now farming hamlet of Townlake which is only a short walk to the 14th century granite bridge crossing the River Tamar on the Devon/Cornwall border.
                        </p>
                        
                    </Col>
                </Row>
                {/* <Row>
                    <Col>
                        <h2>Dining</h2>
                    </Col>
                </Row>
                <Row>
                    <Col>
                        <p>Locally for good food and pub lovers, Rhylla Cottage is perfectly located only a 5 minute stroll from The Royal Inn. The 15th Century pub, reputedly visited by Charles I during the English Civil War, is now known for its fine ales and large portions of excellent food!</p>
                      
                        <p>Tavistock also has a large variety of bars, restaurants and cafes - only 15 minutes by car.</p>

                        <p>If you prefer, our accommodation has cooking facilities including a hob oven, microwave and kettle.</p>
                    </Col>
                </Row> */}
            </Container>
        </div>
    </Layout>
)

export default OurLocation;
